body,
html {
  height: 100%;
}
.chart-sheet {
  height: 100%;
  overflow: hidden;
  .uploading {
    position: fixed;
    bottom: 1.3em;
    left: 45%;
    width: 90px;
    height: 90px;
    background: #fff361;
    line-height: 83px;
    text-align: center;
    font-size: 3em;
    box-shadow: 0 10px 8px -3px rgba(0, 0, 0, 0.3);
    color: white;
    text-shadow: 0 1px 2px #847b13;
    transition: 0.42s all;
    transition-timing-function: cubic-bezier(0.63, 0.15, 0.49, 0.87);
    transform: translateY(260px);
  }
  .uploading.vis {
    transform: translateY(0px);
    animation: shake 0.25s 0.42s;
    animation-iteration-count: infinite;
    animation-direction: alternate;
    animation-timing-function: ease-in-out;
  }
  > div {
    float: right;
    width: 68%;
    height: 100%;
  }
  > div:nth-child(1) {
    padding: 3em 4em;
    overflow: auto;
    .t-menu {
      position: fixed;
      right: 1.3em;
      top: 1.6em;
      transition: 0.3s all;
      transition-timing-function: cubic-bezier(0.63, 0.15, 0.49, 0.87);
      transform: translateX(100px);
    }
  }
  > div:nth-child(1):hover {
    .t-menu {
      transform: translateX(0px);
    }
  }
  > div:nth-child(2) {
    width: 32%;
    padding: 3em 3em 0;
    background: #e8e8e8;
    box-shadow: 0 0 3px 0 #717171;
    overflow: auto;
  }
}
.sheet {
  margin: 0;
  padding: 0;
  list-style: none;
  li {
    position: relative;
    .button {
      position: absolute;
      right: -1.8em;
      bottom: -1px;
      visibility: hidden;
      transition: 0.2s all;
      transition-timing-function: cubic-bezier(0.63, 0.15, 0.66, 1.29);
      transform: scale(0);
    }
    .button.vis {
      visibility: visible;
      transform: scale(1);
    }
  }
  .label {
    display: inline-block;
    width: 50%;
    color: #2d2d2d;
    padding: 2px 4px;
    border-bottom: 2px solid black;
  }
  .input-control {
    width: 50%;
    margin: 0;
  }
}
.ctl-btns {
  margin: 1.3em 0;
  button {
    width: 48%;
  }
  button:first-child {
    margin-right: 4%;
  }
}
@keyframes shake {
    from {
      transform: rotate(5deg);
    }
    to {
      transform: rotate(-5deg);
    }
}



